.Header{
    text-align: center;
}

.Header h1{
    color: rgb(85, 11, 255);
    text-align: center;
    font-size: 42px;
    padding: 10px;
    font-weight: bolder;
}
.Header nav{
    background-color: blueviolet;
    line-height: 40px;
    font-size: 18px;
}
nav a{
    text-decoration: none;
    margin-right: 20px;
    padding: 8px;
    color: white;
}
nav a:hover{
    background-color: green;
    text-decoration: none;
    color: yellow;
}